import { Injectable  } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { BehaviorSubject } from 'rxjs';

import { User } from '../../models/user';
import { tap } from 'rxjs/operators';

import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AuthorizationService {

  private currUserSubject: BehaviorSubject<User>;
  public currentUser: Observable<User>;

  constructor(private http: HttpClient) {

    this.currUserSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('currentUser')));
    this.currentUser = this.currUserSubject.asObservable();

   }


  login(username: string, password: string) {
    const url = environment.baseUrl + 'webapp/manual_auth/';
    return this.http.post<any>(url,
     { username, password })
    .pipe(tap(res => {
        if (res.token) {
          localStorage.setItem('access_token', res.token);
          localStorage.setItem('profile', JSON.stringify(res));
        }
    }));
  }

  logout(): void {
    localStorage.removeItem('access_token');
    localStorage.removeItem('profile');
    this.currUserSubject.next(null);
  }

  loggedIn(): boolean {
   return localStorage.getItem('access_token') !==  null;
 }

}
