import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthorizationService } from '../../../services/auth/authorization.service';
import { DoctorHomeService } from '../../../services/doctor-home.service';

@Component({
  selector: 'app-doctorhome',
  templateUrl: './doctorhome.component.html',
  styleUrls: ['./doctorhome.component.css']
})
export class DoctorhomeComponent implements OnInit {
  consultas: any = [];
  constructor(
    private auth: AuthorizationService,
    private router: Router,
    private doctorhomeService: DoctorHomeService,
  ) { }
  ngOnInit() {
    if (this.auth.loggedIn() == null) {
      this.router.navigate(['/']);
    }
    this.getConsultas();
    this.doctorhomeService.change.subscribe( consulta => {
      this.consultas = consulta;
    });
  }
  getConsultas(): void {
    this.doctorhomeService.getConsultas().subscribe(
      response => { this.consultas = response;  }
    );
  }

  setAttend(consulta: any): void {
    const user = localStorage.getItem('profile');
    const userprofile = JSON.parse(user);

    const data = {
      attended: userprofile.upk,
      consulta_attended: consulta.pk,
      diagnostic: 'Revision'
    };

    this.doctorhomeService.setAttender(data).subscribe(
      response => {
        alert(response);
      }
    );

  }


}


