import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ConsultaService } from '../../../services/consulta.service';

@Component({
  selector: 'app-formconsulta',
  templateUrl: './formconsulta.component.html',
  styleUrls: ['./formconsulta.component.css']
})
export class FormconsultaComponent implements OnInit {
  sintomForm: FormGroup;
  patientForm: FormGroup;
  mypatients: any = [];
  consulta: any = {};
  user = localStorage.getItem('profile');
  userprofile = JSON.parse(this.user);
  curpatient = {};


  constructor(
    private formBuilder: FormBuilder,
    private consultaService: ConsultaService,
  ) { }

  ngOnInit() {
    this.sintomForm = this.formBuilder.group({
      sintoma: ['', Validators.required ],
      zona: ['', Validators.required ],
      causa: ['', Validators.required ]
    });

    this.patientForm = this.formBuilder.group({
      name: ['', Validators.required],
      age: ['', Validators.required],
      gender: ['', Validators.required]
    });

    this.consultaService.getPatients().subscribe(
      response => {
        this.mypatients = response;
      });

  }

  setConsulta(patient: any) {
    this.curpatient = patient;
    this.consultaService.setConsulta().subscribe(
      response => {
        this.consulta = response;
      }
      );
  }

  newPatient(): void {
    const data = {
      name: this.patientForm.controls.name.value,
      age: this.patientForm.controls.age.value,
      gender: this.patientForm.controls.gender.value,
      userpk: this.userprofile.upk
    };
    this.curpatient = data;
    this.consultaService.newPatient(data).subscribe(
      response => {
        this.consulta = response;
      }
    );
  }

}
