import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';

import { LoginComponent } from './components/auth/login/login.component';
import { HomeComponent } from './components/home/home.component';
import { NuevaconsultaComponent } from './components/nuevaconsulta/nuevaconsulta.component';
import { DoctorhomeComponent } from './components/doctor/doctorhome/doctorhome.component';
import { MisconsultasComponent } from './components/doctor/misconsultas/misconsultas.component';
import { MyappointmentsComponent } from './components/patient/myappointments/myappointments.component'

const routes: Routes = [
  {path: '', component: LoginComponent},
  {path: 'home', component: HomeComponent},
  {path: 'nuevaconsulta', component: NuevaconsultaComponent},
  {path: 'doctor', component: DoctorhomeComponent },
  {path: 'attended', component: MisconsultasComponent },
  {path: 'myappointments', component: MyappointmentsComponent },
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
