import { Component, OnInit } from '@angular/core';
import { ConsultaService } from '../../services/consulta.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import {MatSnackBar} from '@angular/material/snack-bar';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';



@Component({
  selector: 'app-nuevaconsulta',
  templateUrl: './nuevaconsulta.component.html',
  styleUrls: ['./nuevaconsulta.component.css']
})
export class NuevaconsultaComponent implements OnInit {

  isCollapsed = false;
  consulta: any = [];
  closeResult: string;
  isintomas: any;
  currConsulta: any = {};
  response: any;
  symForm: FormGroup;
  SignalForm: FormGroup;
  doctors:any=[];
  patientForm: FormGroup;
  curdc: any = {};


  constructor(
    private consultaService: ConsultaService,
    private snackBar: MatSnackBar,
    private modalService: NgbModal
  ) { }


  openSnackBar(message: string, action: string) {
    this.snackBar.open(message, action, {
      duration: 2000,
    });
  }


  ngOnInit() {

    this.symForm = new FormGroup({
      symptom: new FormControl(null, Validators.required),
      zone: new FormControl(null),
      intencity: new FormControl(null, Validators.required)
    });

    
    this.patientForm = new FormGroup({
      name: new FormControl('Nombre', Validators.required),
      age: new FormControl('10'),
      gender: new FormControl(null, Validators.required)
    });



    this.SignalForm = new FormGroup({
      name: new FormControl(null, Validators.required),
      measure: new FormControl(null),
      units: new FormControl(null, Validators.required)
    });
    this.consulta = {
      patient:{
        name:'Unnamed',
        age:'12',
        gender:'M'
      },
      symptoms:[],
      signals:[],
      info:{},
    };

    this.doctors = [];
    this.setConsulta();
  }



  onSubmit() {
    return;
  }

  open(content, mydoctor) {
    this.curdc = mydoctor;
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;

    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  
  setConsulta(): void {

    const appoinment = this.getCurrAppoinment();
    if(appoinment.pk)
    return;
    this.consultaService.setConsulta().subscribe(
      response => { 
        localStorage.removeItem('appoinment');
        localStorage.setItem('appoinment', JSON.stringify(response));    
      }
    );
  }

getCurrAppoinment(){
  const appoinment = localStorage.getItem('appoinment');
  const appoinmentjson = JSON.parse(appoinment);
  return appoinmentjson;
}

  
  getDoctors(): void {
    const appoinment = this.getCurrAppoinment();
    this.consultaService.getDoctors(appoinment.pk).subscribe(
      response => { this.doctors = response;  }
    );
  }


  setSymptom(): void {
    const newsin = {
      symptom:this.symForm.controls['symptom'].value,
      zone:this.symForm.controls['zone'].value,
      intencity:this.symForm.controls['intencity'].value

    };
    const appoint = this.getCurrAppoinment();
    this.consultaService.setSymtom(appoint.pk, newsin).subscribe(
      response => { 
        newsin['pk'] = response.pk;
        this.consulta.symptoms.push(newsin);
        this.symForm.reset();
        this.getDoctors();
        this.openSnackBar('Info actualizada correctamente.','x');

          }
    );
  }

  rmSym(thSym): void{
    this.consultaService.rmSymtom(thSym.pk).subscribe(
      response => { 
        const inx = this.consulta.symptoms.indexOf(thSym);
        this.consulta.symptoms.splice(inx,1);
        this.getDoctors();
        this.openSnackBar('Info actualizada correctamente.','x');
      }
    );    
  }

  setPatient(): void {
    const appoint = this.getCurrAppoinment();
    const infop = {
      name:this.patientForm.controls['name'].value, 
      age:this.patientForm.controls['age'].value, 
      gender: this.patientForm.controls['gender'].value
    };
    this.consultaService.setPatient(appoint.pk,infop).subscribe(
      response => { 
        console.log(response);
        this.openSnackBar('Info actualizada correctamente.','x');

      }
    );    
  }

}

