import { Injectable, Output, EventEmitter } from '@angular/core';
import { Consulta } from '../models/consulta';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { HttpHeaders } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class ConsultaService {

  @Output() change: EventEmitter<any> = new EventEmitter();

  consulta: Consulta;
  response: any;
  public userinfo = localStorage.getItem('access_token');

  constructor(
    private http: HttpClient
  ) { }


  getConsulta(pk: number) {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        Authorization: 'JWT ' + this.userinfo
      })
    };


    const url = environment.baseUrl + 'webapp/get_user/' + pk + '/';
    return this.http.post<any>(url, {}, httpOptions).pipe();
  }

  getPatients() {
    const user = localStorage.getItem('profile');
    const userprofile = JSON.parse(user);
    const url = environment.baseUrl + 'webapp/getpatients/' + userprofile.upk + '/';
    return this.http.get<any>(url).pipe();


  }

  getDoctors(appoinmentpk) {
    const url = environment.baseUrl + 'webapp/patient/doctors/'+appoinmentpk+'/';
    return this.http.get<any>(url).pipe();
  }



  setConsulta() {
    const user = localStorage.getItem('profile');
    const userprofile = JSON.parse(user);
    const url = environment.baseUrl + 'webapp/setconsulta/';
    const data = {
      userapply: userprofile.upk,
      status: 0
    };
    return this.http.post<any>(url, data).pipe();
  }



  setSymtom(consultapk, syntom) {
    const url = environment.baseUrl + 'webapp/addsintom/'+consultapk+'/';
    const data = {
      consultapk:consultapk,
      sintoma: syntom.symptom,
      intensidad:syntom.intencity,
      zona:syntom.zone
    };
    return this.http.post<any>(url, data).pipe();
  }

  setPatient(consultapk, patient_info) {
    const url = environment.baseUrl + 'webapp/setconsulta/';
    const data = {
      consultapk:consultapk,
      patient_info:patient_info
    };
    return this.http.put<any>(url, data).pipe();
  }


  rmSymtom(syntompk) {
    const url = environment.baseUrl + 'webapp/addsintom/'+syntompk+'/';
    return this.http.delete<any>(url,{}).pipe();
  }



  addSintom(sintom: any) {
    this.consulta.sintoms.push(sintom);
    return sintom;
  }


  newPatient(data: any) {
    const url = environment.baseUrl + 'webapp/newpatient/';
    return this.http.post<any>(url, data).pipe();
  }



}
