import { Injectable, Output, EventEmitter } from '@angular/core';
import { Consulta } from '../models/consulta';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { HttpHeaders } from '@angular/common/http';



@Injectable({
  providedIn: 'root'
})
export class DoctorHomeService {

  @Output() change: EventEmitter<any> = new EventEmitter();

  consulta: Consulta;
  response: any;
  public userinfo = localStorage.getItem('access_token');


  constructor(
    private http: HttpClient,
  ) { }


  getConsultas() {
    const user = localStorage.getItem('profile');
    const userprofile = JSON.parse(user);
    const url = environment.baseUrl + 'webapp/consultas/?upk=' + userprofile.upk;
    const response = this.http.get<any>(url).pipe();
    return response;
  }

  setAttender(data: any) {
    const url = environment.baseUrl + 'webapp/attend/';
    const response = this.http.post<any>(url, data).pipe();
    return response;

  }

}
