import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MyappointmentsService } from '../../../services/patient/myappointments.service';

@Component({
  selector: 'app-myappointments',
  templateUrl: './myappointments.component.html',
  styleUrls: ['./myappointments.component.css']
})
export class MyappointmentsComponent implements OnInit {
  consultas: any = [];
  panelOpenState = false;
  constructor(
    private router: Router,
    private myappointmentsService: MyappointmentsService,

  ) { }

  ngOnInit() {
    this.getConsultas();
    this.myappointmentsService.change.subscribe( consulta => {
      this.consultas = consulta;
    });


  }

  getConsultas(): void {
    this.myappointmentsService.getConsultas().subscribe(
      response => { this.consultas = response;  }
    );
  }


}
